import React, { Component } from 'react';
import { Card, Input, Button } from 'reactstrap';
import { Logger , reloadPage , humanFileSize } from '../../Utils/Utils';
import { requestHeaderGET , requestHeaderFilePOST } from '../../Rest-API/Header';
import Loading from "../../Loading";
import Dropzone from 'react-dropzone';
import {useDropzone} from 'react-dropzone';
import "./Filevault.css";
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';

const FILE_NAME = "Filevault.js";

class Filevault extends Component {

  constructor(props) {
    super(props);

    this.state = {
        files: [],
        loadingModalVisible: false,
        loadingModalState: 0,
        failureModalVisible: false,
    };
  }

  uploadMultipleFiles = () => {

    const formData = new FormData();
    for(let index = 0; index < this.state.files.length; index++) {
        formData.append("files", this.state.files[index]);
    };

    //enable loader modal
    this.setState({
      loadingModalVisible: true,
      loadingModalState: 0
    });

    fetch("http://192.168.1.109/filevault/uploadMultipleFiles", requestHeaderFilePOST(formData))
    .then(res => { if (res.status != 200) { this.setState({ failureModalVisible: true }) } })
    .then(
      (result) => {
        Logger(FILE_NAME, "uploadFile", "Fetch", "result", result);
        this.setState({
          loadingModalState: 1
        });

        setTimeout(()=>{
          this.setState({
            loadingModalVisible: false
          });
          reloadPage();
        } , 2000);
      },
      (error) => {
        Logger(FILE_NAME, "uploadFile", "Fetch", "error", error);

        this.setState({
          loadingModalState: 2
        });

        setTimeout(()=>{
          this.setState({
            loadingModalVisible: false,
            failureModalVisible: true
          });
        } , 2000);
      }
    );
  }
  getAllDroppedFiles = (files) => {
    const updatedList = files.concat(this.state.files);
    this.setState({
        files: updatedList
    });
  }

    render() {
      Logger(FILE_NAME, "render", "", "this.state", this.state);

      return (
          <Card id="FileUpload">
              <div className="wrapper">
                <Dropzone onDrop={this.getAllDroppedFiles} >
                      {({getRootProps, getInputProps}) => (
                          <section>
                              <Card  {...getRootProps()}>
                                <input {...getInputProps()} webkitdirectory={true} mozdirectory={true} directory={true} multiple={true} />
                                <div className="upload">
                                  <p>Drag 'N Drop the files here or <span className="upload__button">Browse</span></p>
                                </div>
                              </Card>
                          </section>
                      )}
                  </Dropzone>
                  
                  <Button onClick={this.uploadMultipleFiles}>UPLOAD FILES</Button>

                  <h1>Dropped file(s) : </h1>
                  <table border = "1">
                    <thead>
                      <tr>
                          <td>NAME</td>
                          <td>DIR-path</td>
                          <td>SIZE</td>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        (this.state.files.length > 0) &&
                          this.state.files.map((droppedFile ,index) => 
                            <tr key={"df_" + index}>
                              <td>{droppedFile.name}</td>
                              <td>{droppedFile.path}</td>
                              <td>{humanFileSize(droppedFile.size)}</td>
                            </tr>
                        )
                      }
                    </tbody>
                  </table>

                  
              </div>
              
              <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
              <FailureModal visible={this.state.failureModalVisible} />
          </Card>
          );
    }

}
export default Filevault;